import './CreateLinks.scss'
import $ from 'jquery'
import { db } from '../resources/Firebase'
import { streamer } from '../resources/Config'
import { Capitalize } from '../resources/Helpers'
import { useEffect, useState } from 'react'

const CreateLinks = () => {
    const [delButtons, setDelbuttons] = useState([]);

    useEffect(() => {
        const arrayDatos = db.collection("users").doc(streamer).collection("buttons").orderBy("timestamp")
        .onSnapshot((arrayDelButtons) => {
            $('#eliminar-botones').empty()
            setDelbuttons(boton => boton.concat(arrayDelButtons.docs))
        }, (error) => {
            console.log(error)
        });

        return () => arrayDatos()
    }, [])

    function BorrarBoton(nombreBoton) {
        db.collection("users").doc(streamer).collection("buttons").doc(nombreBoton.toLowerCase()).delete();
    }

    function NuevoBoton() {
        var boton = {}
        boton.nombre = $('#boton-nombre').val()
        boton.url = $('#boton-url').val()
        boton.tipo = $('#boton-tipo').val()
        boton.icono = $('#boton-icono').val()

        if (!(boton.nombre==='' || boton.url==='' || boton.tipo===null || boton.icono===null)) {    
            $('#eliminar-botones').empty()
            db.collection("users")
            .doc(streamer)
            .collection("buttons")
            .doc(boton.nombre.toLowerCase())
            .set({
                name: Capitalize(boton.nombre),
                link: boton.url,
                type: boton.tipo,
                icon: boton.icono,
                timestamp: new Date()
            }, (error) => {
                console.log(error)
            })
            .then(() => {
                $('#boton-nombre,#boton-url').val('')
                $('#boton-tipo,#boton-icono').val('1')
                $('#boton-enviado').text('Botón añadido').addClass('success-form')
            })
        } else {
            $('#boton-enviado').text('Rellena todos los campos').addClass('error-form')
        }
    }

    return (
        <>
            <h5 className="text-light titulo-h4 mt-4">Nuevo botón</h5>
            <input className="form-control form-control-sm mt-2" id="boton-nombre" type="text" name="nombre" placeholder="Nombre..." required />
            <input className="form-control form-control-sm mt-2" id="boton-url" type="url" name="url" placeholder="https://..." required />
            <select className="custom-select custom-select-sm mt-2" defaultValue="1" id="boton-tipo" name="tipo" required>
                <option value="1" disabled="disabled" required>Tipo de botón...</option>
                <option value="2">Normal</option>
            </select>
            <select className="custom-select custom-select-sm mt-2" defaultValue="none" id="boton-icono" name="icono" required>
                <option value="none" disabled="disabled" required>Icono de botón...</option>
                <option value="twitch">Twitch</option>
                <option value="youtube">Youtube</option>
                <option value="discord">Discord</option>
                <option value="tiktok">TikTok</option>
                <option value="onlyfans">OnlyFans</option>
                <option value="tienda">Tienda</option>
                <option value="generico">Otro</option>
            </select>
            <div>
                <button className="btn btn-1 btn-sm mt-2" id="addboton" onClick={NuevoBoton}>Añadir enlace</button>
                <div className="small mt-1" id="boton-enviado"></div>
            </div>
            <h5 className="text-light titulo-h4 mt-4">Borrar botones</h5>
            <div id="eliminar-botones">
                {
                    delButtons.length > 0 ? (
                        delButtons.map((delButton,index) => (
                            <button id={index} key={index} className="btn btn-2 mr-2 mb-2" onClick={() => BorrarBoton(delButton.data().name)}>
                                {delButton.data().name} <sup>x</sup>
                            </button>
                        ))
                    ) : (null)
                }
            </div>
        </>
    )
}

export default CreateLinks;